    /* public length = Object.keys(obj).length; */
    GetlastEntry(allElement:any[])
    {
        for (let [key, value] of Object.entries(allElement)) {
            if(value.length){
                this.currentStateCountry.push(
                    {
                        active: value[value.length-1].confirmed,
                        cases: value[value.length-1].confirmed,
                        casesPerOneMillion: 0,
                        country: key,
                        critical: value[value.length-1].confirmed,
                        deaths: value[value.length-1].confirmed,
                        deathsPerOneMillion: true,
                        recovered: value[value.length-1].confirmed,
                        todayCases: 0,
                        todayDeaths: 0
                    }
                );
            }
        }
    }
    SetCountryData(allElement:any[])
    {
        /* 2020-1-22: {cases: 555, deaths: 17, recovered: 28} */
        /* Afghanistan: Array(77) */
        /* 0: {date: "2020-1-22", confirmed: 0, deaths: 0, recovered: 0} */
        for (let [key, value] of Object.entries(allElement)) {
            let confirmed = 0;
            let deaths =0;
            let recovered=0;
            if(value.length){
                this.countries_temp.push(
                    {
                        active: value[value.length-1].confirmed,
                        cases: value[value.length-1].confirmed,
                        casesPerOneMillion: 0,
                        country: key,
                        critical: value[value.length-1].confirmed,
                        deaths: value[value.length-1].deaths,
                        deathsPerOneMillion: 0,
                        recovered: value[value.length-1].recovered,
                        todayCases: (value[value.length-1].confirmed-value[value.length-2].confirmed),
                        todayDeaths: (value[value.length-1].deaths-value[value.length-2].deaths)
                        /* todayCases: 0, */
                        /* todayDeaths: 0 */
                    }
                );
            }
        }
    }
    calculateSum2(index, array = this.countries) {
        /* calculateSum(index, array = this.countriesHistory) { */
        var total = 0
        var _len = Object.keys(array).length;
        /* for (var i = 0; i < _len; i++) { */
        for (var i in array) {
            if(array[i][index])
                total += array[i][index]
            else
                total += 0;
        }
        return total
    }
    calculateSum1(index, array = this.countries_temp) {
        /* calculateSum(index, array = this.countriesHistory) { */
        var total = 0
        var _len = Object.keys(array).length;
        /* for (var i = 0; i < _len; i++) { */
        for (var i in array) {
            if(array[i][index])
                total += array[i][index]
            else
                total += 0;
        }
        return total
    }
    calculateSum(index, array = this.countries) {
        var total = 0
        let THIS =this;
        for (var i = 0, _len = array.length; i < _len; i++) {
            /* total += array[i][index] */
            if(array[i]["total_cases"])
                this.totalCases += array[i]["total_cases"];//this.calculateSum("total_cases");
            if(array[i]["total_deaths"])
                this.totalDeaths += array[i]["total_deaths"];//this.calculateSum("total_deaths");
            if(array[i]["total_recovered"])
                this.totalRecoveries += array[i]["total_recovered"];//this.calculateSum("total_recovered");
            if(array[i]["total_serious_cases"])
                this.totalCritical += array[i]["total_serious_cases"];//this.calculateSum("total_serious_cases");
            if(array[i]["total_new_cases_today"])
                this.todayCases += array[i]["total_new_cases_today"];//this.calculateSum("total_new_cases_today");
            if(array[i]["total_new_deaths_today"])
                this.todayDeaths += array[i]["total_new_deaths_today"];//this.calculateSum("total_new_deaths_today");
            if(array[i]["total_unresolved"])
                this.unresolved += array[i]["total_unresolved"];//this.calculateSum("total_unresolved");
            this.activeCases += array[i]["total_active_cases"];//this.calculateSum("total_new_deaths_today")
            /* this.casesPer1M += array[i][index];//this.calculateSum("casesPerOneMillion"); */
        }
        this.activeCasesPerMillion =this.ToMillion(this.activeCases); 
        console.log("data");
        /* return total */
    }
    getDaily(index, array = this.countries_temp) {
        var total = 0
        var _len = Object.keys(array).length;
        for (var i in array) {
            this.firstKeyLength = array[i].length;
            total += array[i][array[i].length-1][index];//retry last element
        }
        return total
    }
    sortData(data, sortBy) {
        try {
            const sortProp = sortBy;
            data.sort((a, b) => {
                if (a[sortProp] < b[sortProp]) {
                    return -1;
                } else if (a[sortProp] > b[sortProp]) {
                    return 1;
                }
                return 0;
            })
        } catch (e) {
            console.error("ERROR while sorting", e);
            return data;
        }
        return data
    }
