    BuildLineChartHistory(array=this.timeLine)
    {
        //from Backend githbub format
        /* Afghanistan: Array(85) */
        /* 0: {date: "2020-1-22", confirmed: 0, deaths: 0, recovered: 0} */
        /* 1: {date: "2020-1-23", confirmed: 0, deaths: 0, recovered: 0} */
        /* 2: {date: "2020-1-24", confirmed: 0, deaths: 0, recovered: 0} */
        /* this.firstKeyLength = this.timeLine[Object.keys(this.timeLine)[0]].length; */
        /* for (let column = 0; column < this.firstKeyLength -1; column++) { */
        let index = 0;
        for (let [key, value] of Object.entries(this.timeLine)) {
            /* if(index) break; */
            let  dataset1:any = {};
            let  dataset2:any = {};
            let  dataset3:any = {};
            dataset1.label = key;
            dataset1.fill = true;
            dataset2.label = key;
            dataset2.fill = true;
            dataset3.label = key;
            dataset3.fill = true;
            let dataConfirmed:any[]=[];
            let dataDeath:any[]=[];
            let dataRecoverd:any[]=[];
            /* let datasets:any[]=[]; */
            let j:number = 0;
            for (let [keyTwo, item] of Object.entries(value)) {
                /* if(!index && !keyTwo) // only firt time read data */
                if(!index ) {// only firt time read data
                    var parts =item.date.split('-');
                    this.labels.push(parts[2]+"-"+parts[1]);
                    /* this.labels.push(item.date); */
                }
                /* labels.push(new Date(item.date)); */
                /* if(item.confirmed) */
                dataConfirmed.push(item.confirmed);
                /* if(item.deaths) */
                dataDeath.push(item.deaths);
                /* if(item.recovered) */
                dataRecoverd.push(item.recovered);
                /* cases += value[column].confirmed; */
                /* recoveries += value[column].recovered; */
                /* deaths += value[column].deaths; */
            }
            dataset1.data=dataConfirmed;
            dataset1.label="confirmed";
            dataset1.borderColor= '#21AFDD';
            dataset1.backgroundColor= '#0697c7';
            dataset2.data=dataRecoverd;
            dataset2.label="recovered";
            dataset2.borderColor= '#10c469';
            dataset2.backgroundColor= '#35cc7f';
            dataset3.data=dataDeath;
            dataset3.label="deaths";
            dataset3.borderColor= '#c01515';
            dataset3.backgroundColor= '#ff7e7e';
            /* hoverBackgroundColor: ["#21AFDD", "#ff5b5b", "#10c469"], */
            /* hoverBackgroundColor:['#037297','#c01515','#008140'], */
            /* backgroundColor:["#0697c7","#ff7e7e","#35cc7f"] */
            index++;
            if(key =="US") key="USA";
            this.countriesHistory[key] = {labels:this.labels,   datasets:[dataset1,dataset2,dataset3]};
        }
        /* this.countriesHistory.sort(function(a, b) { return a.TypeId - b.TypeId; }) */
        /* this.data ={labels:labels, datasets:[dataset1,dataset2,dataset3]}; */
    }
    ngOnDestroy() {
        this.zone.runOutsideAngular(() => {
            if (this.pieChart) {
                this.pieChart.dispose();
            }
            if (this.mapChart) {
                this.mapChart.dispose();
            }
            if (this.lineChart) {
                this.lineChart.dispose();
            }
            if(this.radarChart){
                this.radarChart.dispose();
            }
        });
    }
