    @HostListener('window:resize', ['$event'])
    onResize(event) {
        this.innerWidth = window.innerWidth;
        if(this.innerWidth < 601) {
            this.heightExp = 500;
        }
        console.log("After width windows "+this.innerWidth);
    }
    async ngOnInit() {
        this.innerWidth=window.innerWidth;
        this.innerWidth = window.innerWidth;
        if(this.innerWidth < 601) {
            this.heightExp = 500;
        }
        console.log("width windows "+this.innerWidth);
        this.cols = [
            /* { field:
               code', header: 'Id' }, */
            { field: 'title', header: 'Country' ,index:1},
            { field: 'total_cases', header: 'Cases' ,index:2},
            { field: 'total_active_cases', header: 'Active Cases' ,index:3},
            { field: 'total_deaths', header: 'Deaths' ,index:4},
            { field: 'total_new_cases_today', header: 'Cases Today' ,index:5},
            { field: 'total_new_deaths_today', header: 'Deaths Today' ,index:6},
            { field: 'total_recovered', header: 'Recovered',index:7 },
            { field: 'total_serious_cases', header: 'Critical' ,index:8},
            { field: 'total_unresolved', header: 'Unresolved' ,index:9},
            { field: 'source', header: 'source' ,index:1},
            /* { field: 'code', header: 'code' }, */
        ];
        if(false){
            this.showModal();
        }
        this.zone.runOutsideAngular(async () => {
            combineLatest(
                this._getDataService.getAll(this.sortType),
                this._getDataService.getTimelineGlobal()
            )
            .subscribe(([getAllData, getTimeline]) => {
                this.isLoading = false;
                this.isLoadingCountries = false;
                this.isLoadingMap = false;
                this.timeLine =getTimeline;
                /* this.timeLine.sort(function(a, b) { return a.confirmed - b.TypeId; }) */
                this.BuildLineChartHistory();
                let evilResponse:any=getAllData
                evilResponse = evilResponse.countryitems[0];
                // Step 1. Get all the object keys.
                let evilResponseProps = Object.keys(evilResponse);
                // Step 2. Create an empty array.
                // Step 3. Iterate throw all keys.
                let data:[]=[];
                let index = 1;
                for (let prop of evilResponseProps) {
                    if(index == evilResponseProps.length)break;
                    index++;
                    this.countries.push(evilResponse[prop]);
                    /* this.collection = [...this.countries, evilResponse[prop]]; */
                    /* data.push(evilResponse[prop]); */
                }
                //infection History Data
                this.selectedCountry = this.countries[0];
                /* this.data = this.countriesHistory[this.selectedCountry.title]; */
                this.historyData = this.countriesHistory[this.selectedCountry.title];
                //3 infected Countriy
                console.log(this.countries);
                this.calculateSum("total_cases");
                this.finishedCases = this.totalDeaths + this.totalRecoveries;
                this.fuse = new Fuse(this.countries, {
                    shouldSort: true,
                    threshold: 0.6,
                    location: 0,
                    distance: 100,
                    minMatchCharLength: 1,
                    keys: [
                        "title"
                    ]
                });
                //delete alss ellement has ok state
                this.countries.slice(-1,1);
                this.countries= this.sortData(this.countries, "total_cases");
                this.countries.reverse();
                this.loadLineChart(false);
                this.loadRadar();
                this.BuildBarChartMostInfectedCountry();
                this.loadPieChart();
            });
        });
    }
